<route>
{
  "name": "clientDashboard",
  "meta": {
     "title": "Dashboard"
  }
}
</route>
<template>

  <div>

    <v-row>

      <v-col class="col-12 col-md-6 col-lg-5">
        <data-loader :handler="loadActiveCalls">
          <template v-slot:loading>
            <data-card>
              <v-skeleton-loader type="card-heading,list-item,actions"></v-skeleton-loader>
            </data-card>
          </template>
          <template v-slot:default="{ data: { activeCalls: calls } = { } }">
            <data-card title="Active Calls">
              <v-row class="px-4 py-2 mb-0">
                <data-bit class="col-auto py-1" title="Live Now">{{ calls.live }}</data-bit>
                <data-bit class="col-auto py-1" title="Past Hour">
                  <div>{{ calls.hour }}</div>
                  <div class="error--text font-weight-black" style="font-size:11px;line-height:160%"><v-icon x-small class="error--text mr-1">mdi-arrow-down-thick</v-icon>{{ calls.diff }}</div>
                </data-bit>
              </v-row>
              <v-divider/>
              <v-card-actions>
                <v-btn text to="/reviews" color="primary">Go To Agents</v-btn>
              </v-card-actions>
            </data-card>
          </template>
        </data-loader>
      </v-col>

      <v-col class="col-12 col-md-6 col-lg-5 mr-auto">
        <data-loader :handler="loadAwaitingCalls">
          <template v-slot:loading>
            <data-card>
              <v-skeleton-loader type="card-heading,list-item,actions"></v-skeleton-loader>
            </data-card>
          </template>
          <template v-slot:default="{ data: { awaitingCalls: calls } = { } }">
            <data-card title="Calls Awaiting Review">
              <v-row class="px-4 py-2 mb-0">
                <data-bit class="col-auto py-1" title="Urgent Issues"><span class="error--text">{{ calls.urgent }}</span></data-bit>
                <data-bit class="col-auto py-1" title="Total Queued">{{ calls.queued }}</data-bit>
                <data-bit class="col-auto py-1" title="Completed">
                  <div>{{ calls.completed }}</div>
                  <div class="success--text font-weight-black" style="font-size:11px;line-height:160%"><v-icon x-small class="success--text mr-1">mdi-arrow-up-thick</v-icon>{{ calls.diff }}</div>
                </data-bit>
              </v-row>
              <v-divider/>
              <v-card-actions>
                <v-btn text to="/queue" color="primary">Fix Issues In Review Queue</v-btn>
              </v-card-actions>
            </data-card>
          </template>
        </data-loader>
      </v-col>

    </v-row>

    <v-row>

      <v-col class="col-12 col-md-6 col-lg-5">
        <data-loader :handler="loadAgentUsage">
          <template v-slot:loading>
            <data-card>
              <v-skeleton-loader type="card-heading,list-item,image,actions"></v-skeleton-loader>
            </data-card>
          </template>
          <template v-slot:default="{ data: { agentUsage: data } = { } }">
            <data-card title="Agent Usage">
              <v-row class="px-4 py-2 mb-0">
                <data-bit class="col-auto py-1" title="Agents Available">{{ data.available }}</data-bit>
                <data-bit class="col-auto py-1" title="Time Spent At Capacity">
                  <div>{{ data.spent }}</div>
                  <div class="error--text font-weight-black" style="font-size:11px;line-height:160%"><v-icon x-small class="error--text mr-1">mdi-arrow-down-thick</v-icon>{{ data.diff }}</div>
                </data-bit>
                <div class="col-12 pt-0 pb-1 text-caption">Suggestion: <a>Increase Agent Count to 25</a></div>
              </v-row>
              <v-divider/>
              <apexchart :options="agentChart(data.chart.map(({ label }) => label))" :series="data.chart.map(({ value }) => value)" height="160px" class="py-2"></apexchart>
              <v-divider/>
              <v-card-actions>
                <v-btn text to="/queue" color="primary">Go To Agents</v-btn>
              </v-card-actions>
            </data-card>
          </template>
        </data-loader>
      </v-col>

      <v-col class="col-12 col-md-6 col-lg-5 mr-auto">
        <data-loader :handler="loadSources">
          <template v-slot:loading>
            <data-card>
              <v-skeleton-loader type="card-heading,list-item,image,actions"></v-skeleton-loader>
            </data-card>
          </template>
          <template v-slot:default="{ data: { sources: data } = { } }">
            <data-card title="Source Distribution">
              <apexchart :options="sourceChart(data.chart.map(({ label }) => label))" :series="data.chart.map(({ value }) => value)" height="270px"></apexchart>
              <v-divider/>
              <v-card-actions>
                <v-btn text to="/queue" color="primary">Go To Source Performance</v-btn>
              </v-card-actions>
            </data-card>
          </template>
        </data-loader>
      </v-col>

    </v-row>

    <v-row>

      <v-col class="col-12 col-lg-10">

        <data-loader :handler="loadTrends">
          <template v-slot:loading>
            <data-card>
              <v-skeleton-loader type="card-heading,table-tbody,table-tfoot"></v-skeleton-loader>
            </data-card>
          </template>
          <template v-slot:default="{ data: { trends: data } = { } }">
            <data-card title="Revenue Trends">
              <apexchart :options="trendsChart(data.graph[0].x)" :series="data.graph.map(({ label, y }) => ({ name: label, data: y }))" height="330px"></apexchart>
              <v-divider/>
              <v-card-actions>
                <v-btn text to="/queue" color="primary">Go To Reports</v-btn>
              </v-card-actions>
            </data-card>
          </template>
        </data-loader>
      </v-col>

    </v-row>

  </div>

</template>

<script>

import DataBit from '@/components/DataBit'
import DataCard from '@/components/DataCard'

const sheetOptions = {
  legend: {
    show: false
  },
  colors: [
    '#A3A0FB',
    '#6fd8f8',
    '#a0de78',
    '#facd6d',
    '#FF8373',
    '#ff9dea'
  ]
}

const chartOptions = {
  animations: {
    enabled: true,
    speed: 400,
    animateGradually: false
  },
  toolbar: {
    show: false
  }
}

const textColor = '#333'

const agentChart = (labels = []) => {
  return {
    labels,
    ...sheetOptions,
    chart: {
      type: 'radialBar',
      ...chartOptions,
      parentHeightOffset: 0
    },
    plotOptions: {
      radialBar: {
        hollow: {
          size: '99%'
        },
        track: {
          margin: -10
        },
        dataLabels: {
          name: {
            show: true,
            fontSize: '14px',
            offsetY: -5
          },
          value: {
            show: true,
            color: textColor,
            fontSize: '16px',
            offsetY: 5
          }
        }
      }
    }
  }
}

const sourceChart = (labels = []) => {
  return {
    labels,
    ...sheetOptions,
    chart: {
      type: 'donut',
      ...chartOptions
    },
    dataLabels: {
      enabled: false
    },
    plotOptions: {
      pie: {
        expandOnClick: false,
        donut: {
          labels: {
            show: true,
            total: {
              show: true,
              label: 'Revenue',
              fontSize: '14px',
              color: textColor,
              formatter: ({ globals }) => `$${new Intl.NumberFormat().format(globals.seriesTotals.reduce((a, b) => a + b, 0))}`
            },
            value: {
              show: true,
              color: textColor,
              fontSize: '14px',
              offsetY: 0,
              formatter: (value) => `$${new Intl.NumberFormat().format(value)}`
            }
          }
        }
      }
    }
  }
}

const trendsChart = (categories = []) => {
  return {
    ...sheetOptions,
    chart: {
      type: 'area',
      ...chartOptions
    },
    markers: {
      size: 4,
      strokeWidth: 2
    },
    dataLabels: {
      enabled: false
    },
    stroke: {
      curve: 'smooth',
      width: 2
    },
    fill: {
      type: 'gradient',
      gradient: {

        opacityFrom: 0.5,
        opacityTo: 0.2
      }
    },
    xaxis: {
      type: 'datetime',
      categories,
      labels: {
        show: true,
        style: {
          colors: '#aaa'
        }
      }
    },
    yaxis: {
      tickAmount: 5,
      labels: {
        show: true,
        style: {
          colors: '#aaa'
        },
        formatter: (num) => `$${Math.abs(num) > 999 ? Math.sign(num) * ((Math.abs(num) / 1000).toFixed(1)) + 'k' : Math.sign(num) * Math.abs(num)}`
      }
    },
    grid: {
      show: true,
      xaxis: {
        lines: {
          show: true
        }
      },
      yaxis: {
        lines: {
          show: true
        }
      }
    },
    tooltip: {
      x: {
        format: 'dd MMM yyyy'
      }
    }
  }
}

export default {
  name: 'Dashboard',
  components: {
    DataBit,
    DataCard
  },

  methods: {
    agentChart (data) {
      return agentChart(data)
    },

    sourceChart (data) {
      return sourceChart(data)
    },

    trendsChart (data) {
      return trendsChart(data)
    },

    loadActiveCalls () {
      return this.$store.dispatch('api/request', 'apiGetSummaryActiveCalls')
    },

    loadAwaitingCalls () {
      return this.$store.dispatch('api/request', 'apiGetSummaryAwaitingCalls')
    },

    loadAgentUsage () {
      return this.$store.dispatch('api/request', 'apiGetSummaryAgentUsage')
    },

    loadSources () {
      return this.$store.dispatch('api/request', 'apiGetSummarySources')
    },

    loadTrends () {
      return this.$store.dispatch('api/request', 'apiGetSummaryTrends')
    }
  }

}
</script>

